.advantages {

	&__top {
		position: relative;
		background-color: $color-primary;
		padding: 70rem 0;

		&::before {
			@media (min-width: 992px) {
				content: '';
				position: absolute;
				top: 0;
				left: 155rem;
				height: 100%;
				border-right: 1px solid #536EBB;
			}
		}
	}


	&__items {
		margin-top: 50rem;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 30rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 20rem;
		}
	}

	&__item {
		background-color: #fff;
		border-radius: 20rem;
		padding: 30rem;

		@media (max-width: 991px) {
			padding: 30rem 15rem;
		}

		&:first-child {
			grid-column: 1/3;
			background-image: url('../img/advantages-bg.png');
			background-size: contain;
			background-position: right;
			background-repeat: no-repeat;

			@media (max-width: 991px) {
				background-size: cover;
			}
		}
	}

	&__icon {
		width: 60rem;
		height: 60rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__subtitle {
		font-weight: 700;
		font-size: 18rem;
		line-height: 125%;
		color: $color-primary;
		margin-top: 16rem;
	}

	&__text {
		font-size: 16rem;
		line-height: 125%;
		color: #000;
		margin-top: 8rem;
		max-width: 320rem;
	}

	&__file {
		display: inline-block;
		font-size: 14rem;
		line-height: 125%;
		margin-top: 25rem;
		color: $color-primary;
		border-bottom: 1rem dashed $color-primary;
	}

	&__coffee {
		position: absolute;
		left: 30rem;
		bottom: 100rem;
		width: 250rem;
		height: 250rem;

		@media (max-width: 991px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__bottom {
		padding: 70rem 0;
	}

	&__content {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__title {
		white-space: nowrap;
	}
}

.more-advantages {
	display: flex;
	justify-content: space-between;
	margin-left: 130rem;

	@media (max-width: 991px) {
		margin: 40rem 0 0 0;
	}

	&__item {
		flex: 0 0 28%;
	}

	&__icon {
		width: 60rem;
		height: 60rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		margin-top: 16rem;
	}
}