.faq {
	position: relative;
	padding: 70rem 0;
	background-color: $color-primary;

	&::before {
		@media (min-width: 992px) {
			content: '';
			position: absolute;
			top: 0;
			right: 155rem;
			height: 100%;
			border-right: 1px solid #536EBB;
		}
	}

	&__items {
		margin-top: 50rem;
	}

	&__item {
		border: 1px solid #fff;
		border-radius: 20rem;
	}

	&__item+&__item {
		margin-top: 16rem;
	}

	&__title {
		position: relative;
		font-size: 18rem;
		line-height: 125%;
		color: #fff;
		padding: 20rem 30rem;
		cursor: pointer;
		transition: all 0.3s ease;

		@media (max-width: 991px) {
			padding: 20rem 50rem 20rem 20rem;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-right-3.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			right: 30rem;
			transform: translateY(-50%);
			width: 12rem;
			height: 22rem;
			transition: all 0.3s ease;
		}

		&.active {
			&:before {
				transform: translateY(-50%) rotate(90deg);
			}
		}
	}

	&__body {
		display: none;
		line-height: 150%;
		padding: 0 30rem 20rem 30rem;
		color: #fff;

		@media (max-width: 991px) {
			padding: 0 50rem 20rem 20rem;
		}
	}

	&__bg {
		position: absolute;
		right: 30rem;
		top: 100rem;
		width: 250rem;
		height: 250rem;

		@media (max-width: 991px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}