.services {
	position: relative;
	background-color: #3E579E;
	padding: 70rem 0;

	&::before {
		@media (min-width: 992px) {
			content: '';
			position: absolute;
			top: 0;
			right: 155rem;
			height: 100%;
			border-right: 1rem solid #536EBB;
		}
	}

	&__items {
		margin-top: 50rem;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 50rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 30rem;
		}
	}

	&__item {
		&:nth-child(1) .services__img {
			&:before {
				background-image: url('../img/svg/services-1.svg');
			}
		}

		&:nth-child(2) .services__img {
			&:before {
				background-image: url('../img/svg/services-2.svg');
			}
		}

		&:nth-child(3) .services__img {
			&:before {
				background-image: url('../img/svg/services-3.svg');
			}
		}

		&:nth-child(4) .services__img {
			&:before {
				background-image: url('../img/svg/services-4.svg');
			}
		}

		&:nth-child(5) .services__img {
			&:before {
				background-image: url('../img/svg/services-5.svg');
			}
		}

		&:nth-child(6) .services__img {
			&:before {
				background-image: url('../img/svg/services-6.svg');
			}
		}

		&:nth-child(7) .services__img {
			&:before {
				background-image: url('../img/svg/services-7.svg');
			}
		}

		&:nth-child(8) .services__img {
			&:before {
				background-image: url('../img/svg/services-8.svg');
			}
		}
	}

	&__top {
		position: relative;
		overflow: hidden;

		&::before {
			@media (min-width: 992px) {
				content: '';
				position: absolute;
				top: -20rem;
				left: 20rem;
				width: 100%;
				height: 100%;
				border: 1px solid #536EBB;
				border-radius: 20rem;
			}
		}

		&:hover {
			.services__img {
				&::before {
					content: none;
				}

				&:after {
					opacity: 1;
					visibility: visible;
				}
			}

			.services__text {
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
			}
		}
	}

	&__text {
		padding: 20rem;
		position: absolute;
		top: 20rem;
		left: 0;
		line-height: 125%;
		color: #fff;
		z-index: 1;
		opacity: 0;
		visibility: hidden;
		transition: all 0.4s ease;
		transform: translateY(100%);
	}

	&__img {
		position: relative;
		width: 255rem;
		height: 255rem;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 20rem 20rem 20rem 0;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			bottom: 20rem;
			left: 20rem;
			width: 60rem;
			height: 60rem;
		}

		&::after {
			content: '';
			background: rgba(0, 0, 0, 0.65);
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 20rem 20rem 20rem 0;
			opacity: 0;
			visibility: hidden;
			transition: all 0.3s ease;
		}
	}

	&__icon {
		position: absolute;
		left: 20rem;
		bottom: 70rem;
	}

	&__desc {
		margin-top: 16rem;
		font-weight: 500;
		font-size: 18rem;
		line-height: 125%;
		color: #fff;
	}

	&__bg {
		position: absolute;
		top: 100rem;
		right: 30rem;
		width: 250rem;
		height: 250rem;

		@media (max-width: 991px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

	}

}