.suitable {
	position: relative;
	padding: 70rem 0;
	background-color: #F3F5FC;

	&::before {
		@media (min-width: 992px) {
			content: '';
			position: absolute;
			top: 0;
			left: 155rem;
			height: 100%;
			border-right: 1rem solid #D8DCE6;
		}
	}

	&__items {
		margin-top: 50rem;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 30rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__item {
		position: relative;
		background: #fff;
		border-radius: 20rem;
		padding: 30rem;
	}

	&__icon {
		width: 60rem;
		height: 60rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__desc {
		margin-top: 25rem;
		line-height: 125%;
	}

	&__application {
		display: block;
		position: absolute;
		top: 30rem;
		right: 30rem;
		font-size: 13rem;
		line-height: 125%;
		color: #C5C6C9;

		&:hover{
			color: $color-secondary;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-right-2.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 25rem;
			right: 0;
			width: 24rem;
			height: 14rem;
		}
	}

	&__bg {
		position: absolute;
		left: 30rem;
		bottom: 100rem;
		width: 250rem;
		height: 250rem;

		@media (max-width: 991px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}