.contacts {
	position: relative;

	&__map {
		position: relative;
		z-index: -1;
		height: 575rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__info {
		position: absolute;
		top: 50%;
		left: 405rem;
		transform: translateY(-50%);
		z-index: 10;
		background-color: $color-primary;
		padding: 40rem;

		@media (max-width: 991px) {
			left: 20rem;
			padding: 30rem 20rem;
		}
	}

	&__title {
		font-weight: 900;
		font-size: 30rem;
		line-height: 115%;
		color: #fff;

		@media (max-width: 991px) {
			font-size: 22rem;
		}
	}

	&__tel {
		margin-top: 40rem;
		display: flex;
		flex-direction: column;

		a {
			line-height: 125%;
			color: #fff;

			&:hover {
				color: $color-secondary;
			}
		}

		a+a {
			margin-top: 5rem;
		}
	}

	&__mail {
		display: inline-block;
		margin-top: 30rem;
		line-height: 125%;
		color: #fff;

		&:hover {
			color: $color-secondary;
		}
	}

	&__text {
		max-width: 250rem;
		margin-top: 30rem;
		line-height: 125%;
		color: #fff;
	}
}