.partners {
	position: relative;
	background-color: #F3F5FC;
	padding: 70rem 0;

	&__item {
		background-color: #fff;
		border-radius: 15rem;
		padding: 30rem;
		min-height: 100rem;
		display: flex;
		align-items: center;
		justify-content: center;

		@media (max-width: 991px) {
			padding: 20rem;
		}

		img {
			@media (max-width: 991px) {
				width: 100rem;
				height: 60rem;
				object-fit: contain;
			}
		}
	}

	&__content {
		position: relative;
	}

	&__bottom {
		margin-top: 50rem;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__desc {
		font-weight: 700;
		font-size: 24rem;
		line-height: 125%;
		color: $color-primary;
		max-width: 390rem;

		@media (max-width: 991px) {
			font-size: 18rem;
			margin-bottom: 20rem;
		}
	}
}