html {
	box-sizing: border-box;
	font-size: 0.052083333333333336vw;

	@media (max-width: 991px) {
		font-size: 0.18vw;
	}
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

$color-primary: #31447C;
$color-secondary: #FBBA07;

body {
	font-size: 16rem;
	min-width: 320rem;
	font-family: "ProductSans", sans-serif;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #000;
	transition: all 0.3s ease;

	&:hover {
		color: $color-primary;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15rem;
	max-width: 1140rem;

	@media (max-width: 991px) {
		max-width: 750rem;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-weight: 900;
	font-size: 40rem;
	line-height: 115%;

	@media (max-width: 991px){
		font-size: 32rem;
	}

	&--primary {
		color: $color-primary;
	}

	&--white {
		color: #fff;
	}
}

.btn {
	position: relative;
	display: inline-block;
	border-radius: 15rem;
	padding: 20rem 30rem;
	overflow: hidden;
	transition: all 0.3s ease;

	&__animate {
		height: 250rem;
		width: 250rem;
		position: absolute;
		top: -20rem;
		left: -120rem;
		background: #fff;
		border-radius: 100%;
		transform: scale(0);
		animation: ripple 4s linear infinite;
	}

	&--primary {
		background-color: $color-primary;
		color: #fff;
		box-shadow: 0px 10px 30px rgba(62, 87, 158, 0.3);

		&:hover {
			background-color: $color-secondary;
		}
	}

	&--yellow {
		background-color: $color-secondary;
		box-shadow: 0px 5px 20px rgba(251, 186, 8, 0.3);

		&:hover {
			background-color: $color-primary;
			color: #fff;
		}
	}
}

@keyframes ripple {
	20% {
		opacity: 0;
		transform: scale(2.5);
	}

	100% {
		opacity: 0;
		transform: scale(2.5);
	}
}