.footer {
	padding: 30rem 0;
	background-color: #F3F5FC;

	&__content {
		display: flex;
		justify-content: space-between;
		align-items: center;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__link {
		font-size: 13rem;
		line-height: 125%;
		color: $color-primary;

		&:hover {
			color: $color-secondary;
		}
	}

	&__link+&__link {
		margin-left: 25rem;
	}

	&__socials {
		display: flex;

		@media (max-width: 991px) {
			margin: 30rem 0;
		}

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 30rem;
			height: 30rem;
			border-radius: 50%;
			background-color: #D8DCE6;

			&:hover {
				background-color: $color-secondary;
			}
		}

		a {
			img {
				width: 13rem;
				height: 17rem;
			}
		}

		a+a {
			margin-left: 9rem;
		}
	}

	&__developed {
		display: flex;
		align-items: center;

		p {
			font-size: 13rem;
			line-height: 125%;
			color: #84868C;
			margin-right: 16rem;
		}

		img {
			width: 97rem;
			height: 30rem;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}