.popup {
	position: fixed;
	padding: 0 15rem;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 10;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;
	}

	&__content {
		background-color: #fff;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		color: #000;
		width: 100%;
		max-width: 540rem;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70rem);
		border-radius: 20rem;
		padding: 50rem;

		@media (max-width: 991px) {
			padding: 50rem 20rem;
		}
	}

	&__close {
		position: absolute;
		right: -40rem;
		top: -40rem;

		@media (max-width: 991px) {
			right: 0;
			top: -60rem;
		}

		img {
			width: 30rem;
			height: 30rem;
		}
	}

	&__title {
		font-weight: 700;
		font-size: 24rem;
		line-height: 125%;
		text-align: center;
		color: $color-primary;
	}

	&__desc {
		max-width: 325rem;
		margin: 30rem auto 0;
		text-align: center;
	}

	&__field {
		margin-top: 30rem;
	}

	&__group {
		display: flex;
		flex-direction: column;
	}

	&__group+&__group {
		margin-top: 30rem;
	}

	&__input {
		font-size: 14rem;
		line-height: 100%;
		color: #84868C;
		border-bottom: 1rem solid #D8DCE6;
		margin-top: 15rem;
		outline: none;
		padding-bottom: 15rem;
	}

	&__label {
		font-weight: 500;
	}

	&__btn {
		margin-top: 30rem;
		width: 100%;
	}

	&-calculate {
		.popup__content {
			max-width: 1110rem;
		}

		&__title {
			margin-bottom: 30rem;
			font-weight: 700;
			font-size: 24rem;
			line-height: 125%;
			color: $color-secondary;
		}

		&__desc {
			font-weight: 700;
			font-size: 24rem;
			line-height: 125%;
			color: $color-primary;
		}

		&__btns {
			margin-top: 70rem;
			display: flex;
			justify-content: space-between;
		}

		&__next {
			text-align: right;
			margin-top: 70rem;
		}

		&__slider {
			margin-top: 30rem;
		}

		&__inputs {
			margin-top: 20rem;
			display: flex;
			justify-content: space-between;
		}

		&__input {
			width: 100%;
			font-size: 18rem;
			line-height: 125%;
			color: #84868C;
			outline: none;
		}

		&__radios {
			margin-top: 40rem;
		}

		&__groups {
			margin-top: 30rem;
		}

		&__group+&__group {
			margin-left: 50rem;

			@media (max-width: 991px) {
				margin-left: 25rem;
			}
		}

		&__group {
			position: relative;
			cursor: pointer;

			&.active .popup-calculate__item {
				color: #000;
			}

			.radios__checkmark {
				left: 0;
			}
		}

		&__item {
			font-size: 18rem;
			line-height: 125%;
			color: #84868C;
			padding-left: 15rem;
		}

		&__text {
			margin-top: 15rem;
			font-size: 18rem;
			line-height: 125%;
			color: #000;
		}
	}

	&__textarea {
		border-bottom: 1px solid #D8DCE6;
		outline: none;
		line-height: 125%;
		color: #84868C;
		padding-top: 16rem;
		resize: vertical;
		min-height: 80rem;
		max-height: 200rem;
	}
}

#popup-calculate__input-1 {
	text-align: right;
}

.radios {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 20rem;
	margin-top: 30rem;

	@media (max-width: 991px) {
		grid-template-columns: repeat(1, 1fr);
	}

	&__label {
		position: relative;
		border: 1rem solid #D8DCE6;
		border-radius: 20rem;
		padding: 40rem 20rem;
		cursor: pointer;

		&.active {
			border-color: $color-primary;

			.radios__item {
				color: #000;
			}

			&:before {
				filter: invert(0);
			}
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 50rem;
			transform: translateY(-50%);
			width: 60rem;
			height: 60rem;
			filter: invert(50%);
		}

		&:nth-child(1):before {
			background-image: url('../img/svg/radio-1.svg');
		}

		&:nth-child(2):before {
			background-image: url('../img/svg/radio-2.svg');
		}

		&:nth-child(3):before {
			background-image: url('../img/svg/radio-3.svg');
		}

		&:nth-child(4):before {
			background-image: url('../img/svg/radio-4.svg');
		}

		&:nth-child(5):before {
			background-image: url('../img/svg/radio-5.svg');
		}

		&:nth-child(6):before {
			background-image: url('../img/svg/radio-6.svg');
		}

	}

	&__input {
		opacity: 0;
		visibility: hidden;

		&:checked~.radios__checkmark {
			border-color: $color-primary;

			&::before {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 10rem;
				height: 10rem;
				border-radius: 50%;
				background-color: $color-secondary;
			}
		}
	}

	&__item {
		color: #84868C;
		padding-left: 90rem;
	}

	&__checkmark {
		position: absolute;
		top: 50%;
		left: 15rem;
		transform: translateY(-50%);
		height: 20rem;
		width: 20rem;
		border: 1px solid #C5C6C9;
		border-radius: 50%;

		@media (max-width: 991px) {
			width: 22rem;
			height: 22rem;
		}
	}
}

.noUi-target {
	height: 4rem;
	border: none;
	box-shadow: none;
	background-color: #D8DCE6;
}

.noUi-connect {
	background-color: $color-secondary;
}

.noUi-horizontal .noUi-handle {
	width: 16rem;
	height: 16rem;
	border-radius: 50%;
	background-color: $color-secondary;
	right: -10rem;
	border: none;
	box-shadow: none;
	cursor: pointer;

	@media (max-width: 991px) {
		right: -4rem;
		top: -6rem;
	}
}

.noUi-handle::after,
.noUi-handle::before {
	content: none;
}