.header {
	background: #fff;
	box-shadow: 0 5rem 30rem rgba(0, 0, 0, 0.1);
	padding: 20rem;

	&__top {
		display: flex;
		justify-content: space-between;

		@media (min-width: 992px) {
			margin-bottom: 30rem;
		}
	}

	&__left {
		display: flex;
		align-items: center;
	}

	&__logo {
		position: relative;
		width: 155rem;
		height: 60rem;
		z-index: 5;

		&::before {
			@media (min-width: 992px) {
				content: '';
				position: absolute;
				top: 50%;
				right: calc(-1 * 15rem);
				transform: translateY(-50%);
				height: 40rem;
				border-right: 1rem solid #d8dce6;
			}
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__desc {
		max-width: 190rem;
		margin-left: 30rem;
		font-size: 13rem;
		line-height: 125%;
		color: #84868C;

		@media (max-width: 991px) {
			display: none;
		}
	}

	&__right {
		display: flex;
		align-items: center;
	}

	&__info {
		display: flex;
		flex-direction: column;
		position: relative;
		z-index: 5;

		@media (max-width: 991px) {
			margin-right: 30rem;
		}
	}

	&__tel {
		font-size: 20rem;
		color: $color-primary;
		transition: all 0.3s ease;

		@media (max-width: 991px) {
			font-size: 18rem;
		}

		&:hover {
			color: $color-secondary;
		}

		span {
			font-weight: 700;
		}
	}

	&__online {
		position: relative;
		padding-left: 10rem;
		display: inline-block;
		margin-left: auto;
		margin-top: 3rem;
		font-size: 13rem;
		color: #84868C;
		text-align: right;

		&::before {
			content: '';
			background-color: #52C41A;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 6rem;
			height: 6rem;
			border-radius: 50%;
		}
	}

	&__callback {
		font-size: 14rem;
		padding: 11rem 20rem;

		@media (min-width: 992px) {
			margin: 0 30rem;
		}

		@media (max-width: 991px) {
			margin: 20rem auto;
			width: 200rem;
		}
	}
}

.menu {
	&__body {
		@media (max-width: 991px) {
			background-color: #fff;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 100rem 20rem 20rem;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fff;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 100rem;
				z-index: 10;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__list {
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li {
			@media (max-width: 991px) {
				border-bottom: 1rem solid #ccc;

				&:first-child {
					border-top: 1rem solid #ccc;
				}
			}

			a {
				font-size: 14rem;
				line-height: 125%;
				color: #000;

				&:hover {
					color: $color-secondary;
				}

				@media (max-width: 991px) {
					display: block;
					padding: 20rem 0;
				}
			}
		}

		&>li+li {
			@media (min-width: 992px) {
				margin-left: 30rem;
			}
		}
	}

	&__icon {
		@media (max-width: 991px) {
			z-index: 5;
			display: block;
			position: relative;
			width: 35rem;
			height: 20rem;
			cursor: pointer;

			span,
			&:before,
			&:after {
				left: 0;
				position: absolute;
				height: 10%;
				width: 100%;
				transition: all 0.3s ease 0s;
				background-color: $color-primary;
				border-radius: 10rem;
			}

			&:before,
			&:after {
				content: "";
			}

			&:before {
				top: 0;
			}

			&:after {
				bottom: 0;
			}

			span {
				top: 50%;
				transform: scale(1) translate(0, -50%);
			}

			&.active span {
				transform: scale(0) translate(0, -50%);
			}

			&.active:before {
				top: 50%;
				transform: rotate(-45deg) translate(0, -50%);
			}

			&.active:after {
				bottom: 50%;
				transform: rotate(45deg) translate(0, 50%);
			}
		}
	}
}