.intro {
	position: relative;
	background-color: #F3F5FC;
	padding-top: 70rem;

	@media (max-width: 991px) {
		padding-top: 35rem;
	}

	&__content {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__left {}

	&__title {
		max-width: 540rem;
		font-weight: 900;
		font-size: 44rem;
		line-height: 115%;
		color: $color-primary;

		@media (max-width: 991px) {
			font-size: 32rem;
		}
	}

	&__desc {
		font-size: 40rem;
		line-height: 115%;
		color: $color-secondary;
		margin-top: 6rem;

		@media (max-width: 991px) {
			font-size: 28rem;
		}
	}

	&__text {
		position: relative;
		margin-top: 57rem;
		font-size: 18rem;
		line-height: 125%;
		color: #000;
		padding-left: 100rem;

		@media (max-width: 991px) {
			margin-top: 30rem;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/chat.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0;
			left: 0;
			width: 84rem;
			height: 60rem;
		}

		&::after {
			content: '';
			background-image: url('../img/svg/arrow-down-2.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50rem;
			right: 175rem;
			width: 78rem;
			height: 78rem;
		}
	}

	&__btn {
		margin-top: 50rem;
		height: 60rem;
	}

	&__right {
		position: relative;
		margin-left: 95rem;

		@media (max-width: 991px) {
			margin: 50rem 0 0 0;
		}
	}

	&__img {
		width: 417rem;
		height: 530rem;

		@media (max-width: 991px) {
			margin: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__name {
		position: absolute;
		top: 135rem;
		left: 13rem;

		@media (max-width: 991px) {
			left: 70rem;
		}

		p {
			font-weight: 700;
			font-size: 22rem;
			line-height: 125%;
			color: $color-secondary;
		}

		i {
			font-size: 14rem;
			line-height: 125%;
			color: #fff;
		}

		span {
			display: block;
			font-weight: 700;
		}
	}
}

.scrip-down {
	@media (max-width: 991px) {
		display: none;
	}

	&::before {
		content: '';
		border-right: 2px solid $color-secondary;
		position: absolute;
		bottom: 0;
		left: 159rem;
		height: 157rem;
	}

	&::after {
		content: '';
		border-right: 2px solid #D8DCE6;
		position: absolute;
		top: 0;
		left: 159rem;
		height: 295rem;
	}

	&__content {
		position: absolute;
		left: 150rem;
		bottom: 165rem;
		width: 100%;
		display: flex;
		flex-direction: column;
	}

	img {
		position: absolute;
		top: -130rem;
		width: 18rem;
		height: 26rem;
	}

	span {
		font-size: 11rem;
		line-height: 13rem;
		letter-spacing: 0.05em;
		color: #84868C;
		text-transform: uppercase;
		position: absolute;
		top: -4rem;
		left: 3rem;
		transform: rotate(-90deg) translate(0, 0);
		transform-origin: left top;
	}
}