.lang {
	border: 1px solid #D8DCE6;
	box-sizing: border-box;
	border-radius: 15px;

	@media (max-width: 991px) {
		width: 200rem;
		margin: 0 auto;
	}

	&__list {
		position: relative;
		display: flex;
		flex-direction: column;
		padding: 6rem 25rem 6rem 6rem;
	}

	@media (max-width: 991px) {
		&__list {
			align-items: flex-start;
		}
	}

	&__link {
		position: relative;
		padding-left: 25rem;
	}

	&__link:before {
		content: '';
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		top: 0;
		left: 0;
		width: 19rem;
		height: 22rem;
		border-radius: 50%;
	}

	&__item--ru .lang__link:before {
		background-image: url('../img/svg/lang-ru.svg');
	}

	&__item--eng .lang__link:before {
		background-image: url('../img/svg/lang-eng.svg');
	}

	&__item--uz .lang__link:before {
		background-image: url('../img/svg/lang-uz.svg');
	}

	&__arrow {
		position: absolute;
		top: 0;
		right: 0;
		width: 25rem;
		height: 100%;
		cursor: pointer;
	}

	&.active .lang__arrow:after {
		transform: translateY(-50%) rotate(180deg);
	}

	&__arrow:after {
		content: '';
		background-image: url('../img/svg/arrow-down-1.svg');
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 7rem;
		width: 11rem;
		height: 7rem;
		display: block;
		transition: all 0.3s ease;
	}
}

.lang__item--eng,
.lang__item--uz {
	display: none;
}

.page-id-15 .lang.active .lang__item--eng,
.page-id-15 .lang.active .lang__item--uz {
	display: block;
}

.page-id-302 .lang__item--ru,
.page-id-302 .lang__item--uz {
	display: none;
}

.page-id-302 .lang.active .lang__item--ru,
.page-id-302 .lang.active .lang__item--uz {
	display: block;
}

.page-id-303 .lang__item--ru,
.page-id-303 .lang__item--eng {
	display: none;
}

.page-id-303 .lang.active .lang__item--ru,
.page-id-303 .lang.active .lang__item--eng {
	display: block;
}

.page-id-302 .lang__item--eng {
	order: -1;
}

.page-id-303 .lang__item--uz {
	order: -2;
}

.page-id-302 .lang__item--ru,
.page-id-15 .lang__item--eng,
.page-id-303 .lang__item--ru {
	margin: 10rem 0;
}