.situations {
	position: relative;
	padding: 70rem 0;

	&::before {
		@media (min-width: 992px) {
			content: '';
			position: absolute;
			top: 0;
			right: 155rem;
			border-right: 1px solid #D8DCE6;
			height: 100%;
		}
	}

	&__items {
		margin-top: 50rem;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 30rem;
	}

	&__item {
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__img {
		width: 100%;
		max-width: 235rem;
		height: 190rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__text {
		font-size: 18rem;
		line-height: 150%;
		margin-left: 30rem;

		@media (max-width: 991px) {
			margin: 20rem 0 0 0;
		}
	}

	&__bottom {
		margin-top: 50rem;
	}

	&__desc {
		font-weight: 700;
		font-size: 18rem;
		line-height: 125%;
		color: $color-primary;
	}

	&__btn {
		margin-top: 40rem;
	}

	&__calc {
		position: absolute;
		top: 96rem;
		right: 23rem;

		@media (max-width: 991px) {
			display: none;
		}

		img {
			width: 250rem;
			height: 250rem;
		}
	}
}