.question {
	padding-top: 70rem;

	&__content {
		display: flex;
		justify-content: space-between;
		align-items: center;

		@media (max-width: 991px) {
			flex-direction: column;
			padding-bottom: 70rem;
		}
	}

	&__text {
		position: relative;
		max-width: 225rem;
		font-weight: 700;
		font-size: 24rem;
		line-height: 125%;
		color: $color-primary;

		@media (max-width: 991px) {
			margin: 50rem 0 150rem 0;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/question.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 166rem;
			height: 250rem;
			z-index: -1;
		}
	}

	&__img {
		width: 336rem;
		height: 600rem;

		@media (max-width: 991px) {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__form {
		position: relative;

		@media (min-width: 992px) {
			padding: 20rem 20rem 0 0;
			margin-bottom: -47rem;
		}

		&::before {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			width: 425rem;
			height: 615rem;
			border: 1px solid $color-secondary;
			border-radius: 20rem;

			@media (max-width: 991px) {
				height: 675rem;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				border-radius: 20rem 20rem 20rem 0;
			}
		}

		.popup__body {
			box-shadow: 0px 0px 60px rgba(0, 0, 0, 0.1);
			border-radius: 20px 20px 20px 0px;
		}

		.popup__content {
			transform: none;
		}

		.popup__title {
			font-size: 40rem;
		}
	}
}