.reviews {
	padding: 70rem 0;

	&__top {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__nav {
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			justify-content: center;
			margin-top: 60rem;
		}
	}

	&__swiper {
		margin-top: 50rem;
	}

	&__img {
		position: relative;
		border-radius: 20rem 20rem 0rem 20rem;
		padding: 20rem 0 0 20rem;
		width: 235rem;
		height: 180rem;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 20rem 20rem 0 20rem;
		}

		&::before {
			content: '';
			position: absolute;
			top: 0px;
			left: 0px;
			width: 235rem;
			height: 180rem;
			border: 1px solid $color-secondary;
			border-radius: 20rem;
			z-index: -1;
		}
	}

	&__bottom {
		position: relative;
		max-width: 100%;
		margin-top: 20rem;
		display: flex;
		justify-content: center;
	}

	&__info {
		text-align: center;
		padding: 0 50rem 0 35rem;
	}

	&__name {
		font-weight: 700;
		font-size: 18rem;
		line-height: 125%;
		color: $color-primary;
	}

	&__desc {
		color: #c5c6c9;
		margin-top: 3rem;
	}

	&__video {
		img {
			width: 43rem;
			height: 43rem;
			position: absolute;
			bottom: 0;
			top: 50%;
			transform: translateY(-50%);
			right: 0;
			transition: all 0.3s ease;
		}

		&:hover {
			.reviews__play {
				opacity: 0;
				visibility: hidden;
			}

			.reviews__play-hover {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__play {

		&-hover {
			opacity: 0;
			visibility: hidden;
		}
	}

	.swiper-pagination {
		display: flex;
		position: inherit;
		margin: 0 30rem;
	}

	.swiper-button-next,
	.swiper-button-prev {
		margin-top: 0;
		position: inherit;
		width: 24rem;
		height: 14rem;
	}

	.swiper-button-next:hover,
	.swiper-button-prev:hover {
		opacity: 1;
		filter: invert(50%);
	}

	.swiper-button-prev {
		background-image: url('../img/svg/arrow-right-2.svg');
		transform: rotate(180deg);
	}

	.swiper-button-next {
		background-image: url('../img/svg/arrow-right-2.svg');
	}
}